import React, { useState, useEffect } from 'react';
import DailyIframe from '@daily-co/daily-js';

function App() {
  const [meeting, setMeeting] = useState<DailyIframe.DailyCall | null>(null);
  const [transcript, setTranscript] = useState('');

  useEffect(() => {
    const dailyRoom = DailyIframe.createFrame({
      showLeaveButton: true,
      iframeStyle: {
        position: 'fixed',
        top: '0',
        left: '0',
        width: '100%',
        height: '100%',
      },
    });

    dailyRoom.join({ url: import.meta.env.VITE_DAILY_ROOM_URL });
    setMeeting(dailyRoom);

    const ws = new WebSocket('ws://localhost:8000/ws');
    ws.onmessage = (event) => {
      setTranscript(prevTranscript => prevTranscript + '\n' + event.data);
    };

    return () => {
      meeting?.destroy();
      ws.close();
    };
  }, []);

  return (
    <div>
      <h1>Orator Meeting App</h1>
      <div>{transcript}</div>
    </div>
  );
}

export default App;